* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: sans-serif;

}

a {
  text-decoration: none;
  color: rgb(97,97,97);
}

li {
  list-style : none;
}
.btns {
  padding: 10px 20px;
  border: 0.6px solid #ccc;
  cursor: pointer;
  border-radius: 5px;
  color: #323232;
  background: transparent;
  margin-right: 6px;
}